<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar search-toolbar">
      <el-form :inline="true" :model="filters">
        <div class="panel-box-left">
          <el-form-item>
            <el-input v-model="filters.loginName" placeholder=" "></el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="filters.userName" placeholder=" "></el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="filters.realName" placeholder=" "></el-input>
          </el-form-item>
          <el-form-item>
            <el-select v-model="filters.typeCode" placeholder="">
              <el-option v-for="item in typeCodeOptions" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-form-item>
        </div>
        <div class="panel-box-right">
          <el-form-item>
            <el-button type="primary" size="small" v-on:click="getUsers"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" v-on:click="rest"></el-button>
          </el-form-item>
        </div>
      </el-form>
    </el-col>

    <el-col :span="24">
      <el-form :inline="true" :model="filters" style="height:45px">
        <el-form-item>
          <el-button type="primary" size="small" @click="handleOpen"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="small" @click="handlePasswordReset"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <div class="table">
      <el-table :data="users" highlight-current-row v-loading="listLoading" height="670"
                @selection-change="handleSelectionChange" style="width: 100%;" stripe>
        <el-table-column inline-template :context="_self" fixed="left" label="" width="150"><span>
             <el-button size="small" @click="handleOpen(row)"></el-button>
             <el-button type="danger" size="small" @click="handleDel(row)"></el-button></span>
        </el-table-column>
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="" width="120">
          <template scope="scope">
            <el-popover trigger="hover" placement="top" effect="light">
              <p>: {{ scope.row.userName }}</p>
              <p>: {{ scope.row.clientName }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag>{{ scope.row.userName }}</el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column prop="loginName" label="" width="120" sortable></el-table-column>
        <el-table-column prop="realName" label="" width="130" sortable></el-table-column>
        <el-table-column prop="cardId" label="" min-width="170" sortable></el-table-column>
        <el-table-column prop="birthday" label="" :formatter="formatBirthday" width="170"
                         sortable></el-table-column>
        <el-table-column prop="email" label="" width="120" sortable></el-table-column>
        <el-table-column prop="mobilePhone" label="" min-width="150" sortable></el-table-column>
        <el-table-column prop="sex" label="" :formatter="formatSex" min-width="150" sortable></el-table-column>
        <el-table-column prop="typeCode" label="" :formatter="formatTypeCode" min-width="150" sortable>
          <template scope="scope">
            <el-tag :type="scope.row.typeCode === 'system' ? 'primary' : 'success'" close-transition>
              {{scope.row.typeCode}}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="isAdmin" label="" :formatter="formatIsAdmin" min-width="150"
                         sortable></el-table-column>
        <el-table-column prop="isActive" label="" :formatter="formatIsActive" min-width="130" sortable>
          <template scope="scope">
            <el-switch
              v-model="isActiveModel"
              on-color="#13ce66"
              off-color="#ff4949'"
              @click.native.change="isActiveSwitch()">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="createUser" label="" min-width="120" sortable></el-table-column>
        <el-table-column prop="createTime" label="" :formatter="formatTime" min-width="170"
                         sortable></el-table-column>
        <el-table-column prop="updateUser" label="" min-width="120" sortable></el-table-column>
        <el-table-column prop="updateTime" label="" :formatter="formatUpdateTime" min-width="170"
                         sortable></el-table-column>
      </el-table>
    </div>

    <!---->
    <div class="pager">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-size="pageSize"
                     :page-sizes="pageSizes"
                     layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <!---->
    <el-dialog :title="editFormTitle" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="loginName">
              <el-input v-model="editForm.loginName" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="userName">
              <el-input v-model="editForm.userName" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="realName">
              <el-input v-model="editForm.realName" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="cardId">
              <el-input v-model="editForm.cardId" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="birthday">
              <el-date-picker type="date" placeholder="" v-model="editForm.birthday"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="sex">
              <el-radio-group v-model="editForm.sex">
                <el-radio class="radio" label="M"></el-radio>
                <el-radio class="radio" label="F"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="mobilePhone">
              <el-input v-model="editForm.mobilePhone" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="email">
              <el-input v-model="editForm.email" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="typeCode">
              <el-select v-model="editForm.typeCode" placeholder="" @change="typeCodeSelectionChange">
                <el-option v-for="item in typeCodeOptions" :label="item.label" :value="item.value"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="clientIds">
              <el-select v-model="editForm.clientIds" multiple placeholder="" :disabled="flag">
                <el-option
                  v-for="item in clientOptions" :label="item.name" :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="isAdmin">
              <el-switch on-text="" off-text="" v-model="editForm.isAdmin"></el-switch>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="isActive">
              <el-switch on-text="" off-text="" v-model="editForm.isActive"></el-switch>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"> </el-button>
        <el-button type="primary" @click.native="handleSave" :loading="editLoading">{{btnEditText}}</el-button>
      </div>
    </el-dialog>
  </section>
</template>


<script>
  import moment from 'moment'
  import {getUsersListPage,getApplicationsListPage, addUser, editUser, removeUser, passwordReset} from '../../api/api';
  export default {
    data() {
      return {
        filters: {
          loginName: '',
          userName: '',
          realName: '',
          typeCode: '',
          page: '',
          pageSize: '',
        },
        users: [],
        total: 0,
        page: 1,
        pageSize: 15,
        pageSizes: [15, 30, 50, 100],
        listLoading: false,
        editFormVisible: false,//
        editFormTitle: '',//
        editForm: {
          id: 0,
          loginName: '',
          userName: '',
          realName: '',
          cardId: '',
          birthday: '',
          mobilePhone: '',
          sex: '',
          typeCode: '',
          isAdmin: false,
          isActive: true,
          password: '',
          email: '',
          clientIds: []
        },
        editLoading: false,
        btnEditText: ' ',
        editFormRules: {
          loginName: [{required: true, message: '', trigger: 'blur'}],
          userName: [{required: true, message: '', trigger: 'blur'}],
          realName: [{required: true, message: '', trigger: 'blur'}],
          cardId: [{required: true, message: '', trigger: 'blur'}],
          mobilePhone: [{required: true, message: '', trigger: 'blur'}],
          typeCode: [{required: true, message: '', trigger: 'blur'}],
        },
        typeCodeOptions: [{
          value: 'system',
          label: ''
        }, {
          value: 'client',
          label: ''
        }],
        clientOptions: [],
        isActiveModel: '',
        multipleSelection: [],
        flag: true,
      }
    },
    methods: {
      //
      formatSex: function (row, column) {
        return row.sex === "M" ? '' : row.sex === "F" ? '' : '';
      },
      formatIsActive: function (row, column) {
        return row.isActive === 1 ? '' : '';
      },
      formatIsAdmin: function (row, column) {
        return row.isAdmin === 1 ? '' : '';
      },
      formatTime: function (row, column) {
        return moment(row.createTime).format('YYYY-MM-DD HH:mm:ss');
      },
      formatUpdateTime: function (row, column) {
        return moment(row.updateTime).format('YYYY-MM-DD HH:mm:ss');
      },
      formatBirthday: function (row, column) {
        return moment(row.birthday).format('YYYY-MM-DD');
      },
      formatTypeCode: function (row, column) {
        return row.typeCode === "system" ? '' : row.typeCode === "client" ? '' : row.typeCode;
      },
      isActiveSwitch: function (row) {
        console.log("--------");
      },
      // 
      getUsers() {
        let _this = this;
        _this.filters.page = _this.page,
          _this.filters.pageSize = _this.pageSize,
          _this.listLoading = true;
        getUsersListPage(_this.filters).then((res) => {
          _this.total = res.data.total;
          _this.users = res.data.rows;
          _this.listLoading = false;
        });
      },
      //
      handleDel: function (row) {
        var _this = this;
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          _this.listLodading = true;
          let para = {id: row.id};
          removeUser(para).then((res) => {
            _this.listLoading = false;
            if (res.code != "S00001") {
              this.$message({
                message: res.message,
                type: 'warning'
              });
            } else {
              _this.$notify({
                title: '',
                message: '',
                type: 'success'
              });
              _this.getUsers();
            }
          }).catch(() => {
          });
        }).catch(() => {
          }
        );
      },
      handleOpen: function (row) {
        var flags;
        let _this = this;
        if (row.isActive == 1) {
          flags = true;
        } else {
          flags = false;
        }
        var admin;
        if (row.isAdmin == 1) {
          admin = true
        } else {
          admin = false
        }
        if (row === undefined || row === null) {
          this.editFormVisible = true;
          this.editFormTitle = '';
          this.editForm.id = 0;
        } else {
          this.editFormVisible = true;
          this.editFormTitle = '';
          this.editForm.id = row.id;
        }
        this.editForm.userName = row === null ? "" : row.userName;
        this.editForm.realName = row === null ? "" : row.realName;
        this.editForm.loginName = row === null ? "" : row.loginName;
        this.editForm.cardId = row === null ? "" : row.cardId;
        this.editForm.birthday = row === null ? "" : row.birthday;
        this.editForm.mobilePhone = row === null ? "" : row.mobilePhone;
        this.editForm.sex = row === null ? "" : row.sex;
        this.editForm.email = row === null ? "" : row.email;
        this.editForm.typeCode = row === null ? "" : row.typeCode;
        this.editForm.isAdmin = row === null ? false : admin;
        this.editForm.isActive = row === null ? true : flags;
        this.editForm.clientIds = row === null ? [] : row.clientIds;
        let para = {
          page:0,
          pageSize:0,
        }
        _this.typeCodeSelectionChange(row.typeCode);
        getApplicationsListPage(para).then((res) => {
          _this.clientOptions = res.data.rows;
        }).catch(() => {
        });
      },
      //  
      handleSave: function () {
        let _this = this;
        _this.$refs.editForm.validate(function (vaild) {
          if (vaild) {
            _this.$confirm('', '', {}).then(() => {
              _this.editLodaing = true;
              _this.btnEditText = '';
              //
              var revokedValue;
              if (_this.editForm.isActive == true) {
                revokedValue = 1;
              } else {
                revokedValue = 0;
              }
              var isAdminValue;
              if (_this.editForm.isAdmin == true) {
                isAdminValue = 1;
              } else {
                isAdminValue = 0;
              }
              let para = {
                id: _this.editForm.id,
                loginName: _this.editForm.loginName,
                userName: _this.editForm.userName,
                realName: _this.editForm.realName,
                cardId: _this.editForm.cardId,
                birthday: _this.editForm.birthday,
                mobilePhone: _this.editForm.mobilePhone,
                sex: _this.editForm.sex,
                typeCode: _this.editForm.typeCode,
                isAdmin: isAdminValue,
                isActive: revokedValue,
                email: _this.editForm.email,
                clientIds: _this.editForm.clientIds
              };
              if (_this.editForm.id == 0) {
                addUser(para).then((res) => {
                  _this.editLoading = false;
                  _this.btnEditText = '';
                  if (res.code != "S00001") {
                    _this.$message({
                      message: res.message,
                      type: 'warning'
                    });
                    _this.editLodaing = false;
                    _this.btnEditText = '';
                  } else {
                    _this.$notify({
                      title: '',
                      message: '',
                      type: 'success'
                    });
                    _this.editFormVisible = false;
                    _this.getUsers();
                  }
                }).catch(() => {
                });
              } else {
                //
                editUser(para).then((res) => {
                  _this.editLoading = false;
                  _this.btnEditText = '';
                  if (res.code === "S00001") {
                    _this.$notify({
                      title: '',
                      message: '',
                      type: 'success'
                    });
                    _this.editFormVisible = false;
                    _this.getUsers();
                  } else {
                    _this.$message({
                      message: res.message,
                      type: 'warning'
                    });
                    _this.editLodaing = false;
                    _this.btnEditText = '';
                  }
                }).catch(() => {
                });
              }
            }).catch(() => {
            });
          }
        })
      },
      handleCurrentChange: function (val) {
        this.page = val;
        this.getUsers();
      },
      handleSizeChange: function (val) {
        this.pageSize = val;
        this.getUsers();
      },
      rest: function () {
        this.filters.loginName = '';
        this.filters.realName = '';
        this.filters.userName = '';
        this.filters.typeCode = '';
      },
      //
      handlePasswordReset: function () {
        var _this = this;
        if (_this.multipleSelection.length == 0) {
          _this.$message({
            message: "",
            type: 'warning'
          });
          return;
        }
        if (_this.multipleSelection.length > 1) {
          _this.$message({
            message: "",
            type: 'warning'
          });
          return;
        }
        _this.$confirm("[" + _this.multipleSelection[0].loginName + "]", '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let param = {id: _this.multipleSelection[0].id};
          passwordReset(param).then((res) => {
            if (res.code != "S00001") {
              _this.$message({
                message: res.message,
                type: 'warning'
              });
            } else {
              _this.$notify({
                title: '',
                message: '',
                type: 'success'
              });
            }
          }).catch(() => {
            console.log(" password rest failure.");
          })
        }).catch(() => {
          console.log(" password rest cancel.");
        })
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      typeCodeSelectionChange(val){
        var _this = this;
        if (val === "client") {
          _this.flag = false;
        } else {
          _this.flag = true;
          _this.editForm.clientIds = [];
        }
      }
    },
    mounted() {
      this.getUsers();
    }

  }


</script>

<style scoped>
  .search-toolbar {
    margin: 10px 0px 10px;
    border: 1px solid rgba(32, 160, 255, 0.19);
    box-sizing: border-box;
  }

  .panel-box-left {
    width: 90%;
    float: left;
  }

  .panel-box-right {
    width: 10%;
    float: right;
    padding-right: 5px;
  }

  .el-row {
    margin-bottom: 5px;

  &
  :last-child {
    margin-bottom: 0;
  }

  }
  .el-col {
    border-radius: 4px;
  }


</style>



